*{box-sizing:border-box}html{height:100%}body{margin:0;padding:0;font-family:sans-serif;font-style:italic;display:flex;flex-direction:column;height:100%}.container{background-color:darkblue;flex-grow:3;display:flex;flex-direction:column;align-items:center;justify-content:space-between}.container__title{color:white;padding:15px;margin:0;font-size:50px}.container__tree{position:relative;bottom:-12px}.container__snow{position:fixed;color:white;font-size:40px;position:absolute;top:-30px;transition:all 5s ease-out}.container__snow_down{transform:translateY(93vh) rotate(500deg)}.container__block{position:relative}.snow{flex-grow:1;background-color:#fff;background-color:white;border:1px solid #000}.line{display:inline-flex;position:absolute}.line:nth-of-type(1){top:85px;left:50%;transform:translate(-50%) rotate(15deg)}.line:nth-of-type(2){top:145px;left:50%;transform:translate(-50%) rotate(15deg)}.line:nth-of-type(3){top:210px;left:50%;transform:translate(-50%) rotate(15deg)}.line:nth-of-type(4){top:275px;left:50%;transform:translate(-50%) rotate(15deg)}.line:nth-of-type(5){top:340px;left:50%;transform:translate(-50%) rotate(15deg)}.line__lamp{position:relative;margin:5px;width:25px;height:25px;background-color:red;border-radius:50%;transform:rotate(-15deg)}.line__lamp::before{content:"";width:2px;height:15px;background-color:gold;position:absolute;left:50%;top:-15px;transform:translateX(-50%)}
